@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
/* base styles */
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
}

.chatbox {
  flex: 1 1;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 20px;
  background-color: #fff;
}

.chat-area {
  display: flex;
  flex-direction: column-reverse;
  overflow-y: auto;
  margin-bottom: 20px;
}

.input-section {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
  margin-top: 20px;
}

.chat-input {
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 5px;
  min-height: 20px;
  max-height: 150px;
  overflow-y: auto;
}

.quick-actions {
  display: flex;
  grid-gap: 10px;
  gap: 10px;
}

.action-button {
  padding: 10px 20px;
  font-size: 14px;
  border: none;
  border-radius: 5px;
  background-color: #ddd;
  cursor: pointer;
}

.conversation-pair {
  margin: 10px 0;
}

.user-message {
  background-color: #f0f0f0;
  padding: 10px;
  border-radius: 5px;
  margin: 5px 0;
}

.model-response {
  display: flex;
  align-items: flex-start;
  grid-gap: 12px;
  gap: 12px;
  margin: 5px 0;
}

.robot-icon {
  width: 30px;
  height: 30px;
  border-radius: 50%;
  object-fit: cover;
  flex-shrink: 0;
  margin-bottom: 5px;
}

.response-content {
  background-color: #e9ecef;
  padding: 0;
  border-radius: 10px;
  flex: 1 1;
}
.sidebar {
  width: 250px;
  background-color: #f8f9fa;
  padding: 20px;
  border-right: 1px solid #ddd;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.app-title {
  font-size: 1.5em;
  margin-bottom: 20px;
  color: #333;
}

/* Action buttons with modern colors */
.action-button {
  width: 100%;
  padding: 10px;
  background-color: #f0f2f5;  /* Light gray background */
  color: #1a1a1a;            /* Dark text */
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-bottom: 10px;
  transition: all 0.2s ease;
  font-weight: 500;         /* Slightly bold text */
}

.action-button:hover {
  background-color: #e4e6e9;
  transform: translateY(-1px);
}

/* New chat button with accent color */
.new-chat-button {
  width: 100%;
  padding: 10px;
  background-color: #0ea5e9;  /* Modern blue */
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  margin-top: 30px;
  margin-bottom: 20px;
  transition: all 0.2s ease;
  font-weight: 500;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
}

.new-chat-button:hover {
  background-color: #0284c7;  /* Slightly darker blue on hover */
  transform: translateY(-1px);
}

/* Rest of the styles */
.chat-history {
  display: flex;
  flex-direction: column;
  grid-gap: 10px;
  gap: 10px;
}

.chat-history-item {
  padding: 10px;
  border-radius: 8px;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  align-items: center;
  transition: background-color 0.2s ease;
}

.delete-chat-button {
  visibility: hidden;
  background: none;
  border: none;
  color: #666;
  cursor: pointer;
  padding: 0 5px;
  opacity: 0.7;
}

.delete-chat-button:hover {
  opacity: 1;
}

/* Show delete button only when hovering over chat history item */
.chat-history-item:hover .delete-chat-button {
  visibility: visible;
}

.chat-history-item:hover {
  background-color: #f0f2f5;
}

.chat-history-item.active {
  background-color: #e4e6e9;
}
.onboarding-container {
    display: flex;
    justify-content: center;
    align-items: center;
    min-height: 100vh;
    background-color: #f8f9fa;
    padding: 20px;
}

.onboarding-form {
    background: white;
    padding: 40px;
    border-radius: 12px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    width: 100%;
    max-width: 500px;
}

.onboarding-form h1 {
    color: #333;
    margin-bottom: 10px;
    text-align: center;
}

.onboarding-form p {
    color: #666;
    margin-bottom: 30px;
    text-align: center;
}

.form-step {
    display: flex;
    flex-direction: column;
    grid-gap: 15px;
    gap: 15px;
}

.form-step input,
.form-step select,
.form-step textarea {
    padding: 12px;
    border: 1px solid #ddd;
    border-radius: 8px;
    font-size: 16px;
}

.form-step textarea {
    min-height: 100px;
    resize: vertical;
}

.form-step button {
    padding: 12px;
    background-color: #0ea5e9;
    color: white;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    font-weight: 500;
    transition: all 0.2s ease;
}

.form-step button:hover {
    background-color: #0284c7;
    transform: translateY(-1px);
}

.form-step button[onClick="prevStep"] {
    background-color: #f0f2f5;
    color: #1a1a1a;
}

.form-step button[onClick="prevStep"]:hover {
    background-color: #e4e6e9;
} 
.app {
  display: flex;
  height: 100vh;
  width: 100vw;
  overflow: hidden;
}

/* Card container */
.popup-card {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: white;
  padding: 30px;
  width: 500px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  z-index: 1000;
  display: flex;
  flex-direction: column;
  grid-gap: 20px;
  gap: 20px;
  overflow-y: auto;
  max-height: -webkit-fit-content;
  max-height: -moz-fit-content;
  max-height: fit-content;
}

.popup-content {
  flex: 0 1 auto;
  overflow-y: auto;
  padding: 10px;
  padding-bottom: 0;
}

.info-section {
  margin-bottom: 10px;
  /* Reduced from 20px */
}

.info-section:last-child {
  margin-bottom: 0;
  /* Remove margin for last section */
}

.info-section h3 {
  margin-bottom: 10px;
  color: #333;
}

.info-section p {
  line-height: 1.5;
  color: #666;
}

.popup-card button {
  margin-top: 0;
  width: 120px;
  align-self: center;
}

/* Background overlay */
.popup-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  /* Semi-transparent dark background */
  z-index: 999;
  /* Makes sure the overlay is behind the popup */
}

/* Button styles inside the popup */
.popup-card button {
  padding: 10px 15px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
  align-self: center;
}

.popup-card button:hover {
  background-color: #0056b3;
}

/* Popup card title */
.popup-card h2 {
  margin-top: 0;
  font-size: 20px;
  font-weight: bold;
  text-align: center;
}

/* Popup content */
.popup-card p {
  text-align: center;
  font-size: 14px;
  margin: 10px 0;
}

/* Card container to position in App.js */
.card_container {
  position: relative;
}

/* Sidebar styling */
.sidebar {
  position: fixed;
  left: 0;
  top: 0;
  width: 250px;
  height: 100vh;
  background-color: #f8f9fa;
  border-right: 1px solid #ddd;
  padding: 20px;
  overflow-y: auto;
  z-index: 1000;
}

/* Chatbox container */
.chatbox {
  flex: 1 1;
  margin-left: 290px !important;
  /* Increased margin to account for sidebar width + padding */
  padding: 20px;
}

/* Ensure the chat area doesn't go under the sidebar */
.chatbox>div {
  max-width: calc(100% - 40px);
  height: 100%;
  margin: 0 auto;
}

.basic-info {
  background-color: #f8f9fa;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 20px;
}

.basic-info p {
  margin: 5px 0;
  text-align: left;
}

.basic-info strong {
  color: #333;
  width: 140px;
  display: inline-block;
}
